.aboutMe {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2%;
    background-image: url("../images/desert.jpg");

}

.aboutMe-summary {
    font-size: 120%;
    line-height: 2;
    margin-bottom: 5%;
}

.about-me-image {
    margin-bottom: 5%;
    height: 200px;
    object-fit: contain;

}

.aboutMe img {
    border-radius: 10px;

}

img {
    border-radius: 10px;
}


